.form {
    margin: 10px auto;
    padding: 5px;
    width: 300px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.label {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.span {
    display: block;
    margin-bottom: 5px;
    font-size: 12px;
    opacity: 0.8;
}

.input {
    height: 20px;
    width: 300px;
    padding: 5px 10px;

    font-size: 14px;
    color: #000;

    outline: none;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 3px;

    &:focus {
        border-color: rgb(111, 111, 207);
    }

    &::placeholder {
        font-size: 12px;
        color: rgba(0, 0, 0, 0.75);
    }
}

.button {
    margin: 15px;
    padding: 10px;
    width: 200px;
    font-weight: 500;

    font-size: 24px;
    color: #fff;
    background-color: #1a73e8;
    border: none;
    border-radius: 6px;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
    transition: background-color, color 250ms linear;

    &:hover,
    &:focus {
        background-color: #000cb6b7;
        color: #000;
    }
}